.art_preview_div {
  max-width: 1.5in;
  height: 2in;
  background-size:1.5in auto;
  padding: 0;
  font: sans-serif;
  color: #111;
  font-weight: bold;
  box-shadow: 0px 0px 5px 5px #f0f0f0;
  float:left;
  margin-right:2em;
}
.art_full {
  max-width: 5in;
}
.bib {
  text-indent: -2em;
  margin-left: 2em;
}
body {
    padding-top: 40px;
    padding-bottom: 20px;
}

.page-header ul {
    padding: 0;
    list-style-type: none;
}

.jumbotron {
  margin: 0 0;
  padding: 5px 5px;
}

.yearlist {
	text-align: center;
	display: inline;
	margin-left: 0;
	padding-left: 0;
}
.yearlist li {
	list-style-type: none;
	display: inline;
	padding-left: 0;
	padding-right: 0.5em;
	border-style: none;
}

.more .yearlist li {
	font-size: 13px;
	line-height: 18px;
}
.yearlist li {
	font-size: 18px;
	line-height: 24px;
}

.pageheader {
	padding-bottom: 4px;
}
.pageheader h1 {
	font-size: 38px;
	border-style: none none solid none;
	border-width: thin;
	border-color: #dddddd;
	padding-bottom: 4px;
}
.publication .pageheader h1 {
	font-size: 18px;
	line-height: 27px;
}
.frontpage .contentarea h1 {
	border-style: none none solid none;
	border-width: thin;
	border-color: #dddddd;
}
.contentarea h1 {
	padding-top: 10px;
}
.contentarea h2 {
	padding-top: 10px;
}
.software .contentarea h1 {
	border-style: none none solid none;
	border-width: thin;
	border-color: #dddddd;
}
.people .contentarea h1 {
	border-style: none none solid none;
	border-width: thin;
	border-color: #dddddd;
}

.mainlinks img {
	padding-left: 9px;
	padding-bottom: 9px;
}

.publicationlinks {
	text-align: center;
	display: inline;
	margin-left: 0;
	padding-left: 0;
}
.publicationlinks li {
	list-style-type: none;
	display: inline;
	padding-left: 0;
	padding-right: 0.2em;
	border-style: none;
}

.bibliography {
	padding-top: 10px;
}

.bibliography .publicationlinks li {
	font-size: 11px;
}
.referencelist .publicationlinks li {
	font-size: 11px;
}
.publicationlinks li {
	font-size: 18px;
}

.mainlinks h1 {
	font-size: 13px;
	line-height: 18px;
	font-weight: normal;
}

.contentarea img.pull-right {
	clear: right;
}

.carousel-imagearea {
	width: 590px;
	height: 243px;
}

div.date {
	font-size: 18px;
	line-height: 36px;
	font-weight: normal;
	color: #999999;
}

.footer {
    padding-left:10px;
	padding-top:10px;
	padding-bottom:10px;
	font-size: 11px;
	color: #999999;
}

.navigate {padding: 0.15em 0 1px 0; margin: 0 1.2em 0 0;
  border-bottom: 1px solid #CC9;
  text-align: left; font: 0.85em Arial, Verdana, sans-serif;}
.navigate li {display: inline;}
.navigate li a, #navigate li span {margin: 0; padding: 0.15em 0.33em 1px; border-left: 1px solid #CC9;}
.navigate li span {background: #E3E3B6; color: #552;}
.navigate li a {background: white; }
.navigate li a:visited:hover {background: rgb(175,142,165);}
.navigate li a:link:hover {background: rgb(114,114,134);}
.navigate a:hover {background: #993; color: white; border-bottom: 1px solid #993; border-top: 1px solid #996;}

a.external:after {
    padding-left: 5px;
    content: url(images/external.png);
}


.current {
  color: green;
}
